@use "variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.section-container {
  margin: 0rem 1rem 0rem 1rem;
  @include m.telefonoSM {
    margin-left: 2rem;
    margin-right: 2rem;
  }
  @include m.telefono {
    margin-left: 2.5rem;
    margin-right: 2.5rem;
  }
  @include m.desktop {
    max-width: 95rem;
    margin-left: auto;
    margin-right: auto;
  }
  @include m.desktopXL {
    max-width: 110rem;
  }
}

h2 {
  color: v.$secundario;
  margin: 1rem;
  text-align: center;
  font-size: 2rem;
  @include m.telefonoSM {
    margin: 2rem;
    font-size: 2.25rem;
  }
  @include m.desktop {
    font-size: 2.50rem;
  }
}

h3,
label,
.modal-title,
.btn {
  font-size: 1.6rem;
  @include m.tablet {
    font-size: 1.75rem;
  }
  @include m.desktop {
    font-size: 2rem;
  }
}

p,
.form-control,
.modal-body,
.invalid-feedback,
.thanks-home
{
  font-size: 1.35rem;
  @include m.tablet {
    font-size: 1.50rem;
  }
  @include m.desktop {
    font-size: 1.75rem;
  }
}

.container {
  max-width: 120rem;
  padding: 0 2rem;
}

a {
  text-decoration: none;
  color: v.$primario;
}

img {
  width: 100%;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

footer {
  margin-top: auto;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
