@use "base/mixins" as m;

.AFFA-info {
  .display-large {
    display: none;
    @include m.telefonoSM {
      display: inline;
    }
  }
  .name-extra {
    display: block;
    @include m.telefonoSM {
      display: inline;
    }
  }
  .affa-text,
  .affa-image {
    margin-right: 1rem;
    margin-left: 1rem;
  }
  .affa-text {
    margin-bottom: 2rem;
  }
  margin-top: 2rem;
  text-align: center;
  img {
    height: 15rem;
    object-fit: cover;
  }
  @include m.telefonoSM {
    .affa-image{
      img{
        height: 26rem;
      }
    }
  }
  @include m.telefono {
    .affa-image{
      img {
        height: 40rem;
        object-fit: cover;
      }
    }
  }
  @include m.tablet {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    .affa-image {
      img {
        height: 30rem;
        object-fit: cover;
      }
    }
    h3 {
      margin: 0;
      margin-top: 1rem;
    }
    p {
      text-align: justify;
      margin-bottom: 1rem;
    }
    .affa-text {
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }
  }
  @include m.desktop {
    .affa-image{
      img{
        height: 35rem;
      }
    }
  }
}

.text-short {
  display: block;
}

.text-long {
  display: none;
}

@include m.desktop {
  .text-long {
    display: block;
  }
  .text-short {
    display: none;
  }
}
