@use "base/mixins" as m;

@include m.telefono {
  #gallery,
  #services,
  #us,
  #contact {
    background-clip: padding-box;
    border-top: 37rem solid transparent;
    margin-top: -37rem;
  }
}

@include m.desktop {
  #gallery,
  #services,
  #us,
  #contact {
    background-clip: padding-box;
    border-top: 10.5rem solid transparent;
    margin-top: -10.5rem;
  }
}

.gallery {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  @include m.telefonoSM {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
  @include m.telefono {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  @include m.desktop {
    grid-template-columns: repeat(4, 1fr);
  }
  @include m.desktopXL {
    grid-template-columns: repeat(4, 1fr);
  }
  .card {
    margin-left: 1rem;
    margin-right: 1rem;
    @include m.telefonoSM {
      margin-left: 0rem;
      margin-right: 0rem;
    }
  }
}
