@use 'base/mixins' as m;
@use 'base/variables' as v;

.btn{
    margin-top: 1rem;
    color: v.$blanco;
    background-color: v.$secundario;
    &:hover{
        background-color: v.$primario;
        color: v.$blanco;
    }
    @include m.tablet {
        padding: 0.5rem 15rem;   
    }
}

.container {
    form {
        margin-bottom: 2rem;
        @include m.telefono {
            margin-left: 2rem;
            margin-right: 2rem;
        }
        @include m.tablet {
            margin-left: 6rem;
            margin-right: 6rem;
        }
        @include m.desktop {
            margin-left: 20rem;
            margin-right: 20rem;
        }
    }
}

.form {
    margin-top: 1rem;
    h2 {
        margin-bottom: 0;
    }
    @include m.tablet {
        margin-bottom: 1rem;
    }
}

textarea{
    height: 10rem;
}

.btn-send {
    background-color: v.$secundario;
    color: white;
}

.form-check-input {
    // width: 1.5rem;
    // height: 1.5rem;
    margin-top: 0.5rem;
    margin-right: 0.5rem;
}

@include m.telefonoSM {
    .form-check-input {
        width: 1.25rem;
        height: 1.25rem;
        margin-top: 0.5rem;
    }
}

@include m.tablet {
    .form-check-input {
        width: 1.5rem;
        height: 1.5rem;
        margin-top: 0.25rem;
    }
}

@include m.desktop {
    .form-check-input {
        width: 1.5rem;
        height: 1.5rem;
        margin-top: 0.5rem;
    }
}

@include m.desktopXL {
    .form-check-input {
        width: 1.5rem;
        height: 1.5rem;
        margin-top: 0.65rem;
    }
}

// .form-check {
//     display: flex;
//     align-items: center;
// }

.aviso {
    margin: 0;
    &:hover {
        color: v.$primario;
    }
}

form {
    a {
        color: v.$secundario
    }
}

.form-control.is-valid, 
.was-validated .form-control:valid,
.was-validated .form-control:valid:focus,
.form-control.is-valid:focus
 {
    border-color: v.$primarioOscuro;
}

.form-control.is-valid:focus, 
.was-validated .form-control:valid:focus {
    -webkit-box-shadow: 0px 0px 7px 1px rgba(55,117,151,0.57);
-moz-box-shadow: 0px 0px 7px 1px rgba(55,117,151,0.57);
box-shadow: 0px 0px 7px 1px rgba(55,117,151,0.57);
}

.form-control.is-valid, 
.form-control.is-valid:focus, 
.was-validated .form-control:valid, 
.was-validated .form-control:valid:focus {
    border-color: v.$secundario;    
}

.form-check-input.is-valid:checked, 
.was-validated .form-check-input:valid:checked,
.form-check-input:checked {
    background-color: v.$secundario;
}

.form-check-input.is-valid, 
.was-validated .form-check-input:valid,
.form-check-input:checked {
    border-color: v.$secundario;
}

.form-control.is-valid, 
.was-validated .form-control:valid {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='rgb(55,117,151)' d='M2.3 6.73L.6 4.53c-.4-1.04.46-1.4 1.1-.8l1.1 1.4 3.4-3.8c.6-.63 1.6-.27 1.2.7l-4 4.6c-.43.5-.8.4-1.1.1z'/%3e%3c/svg%3e");
}

.modal-dialog {
    max-width: 70%;
    margin: 1.75rem auto;
}