@use 'base/mixins' as m;
@use 'base/variables' as v;

svg {
  width: 4.5rem;
  height: 4.5rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  @include m.desktop {
    width: 6rem;
    height: 6rem;
  }
}

.map {
  margin-left: auto;
  margin-right: auto;
  height: 30rem;
  max-width: 120rem;
  @include m.tablet {
    height: 40rem;
    width: 80rem;
  }
  @include m.desktop {
    height: 50rem;
    width: 90rem;
  }
  @include m.desktopXL {
    width: 100rem;
  }
}

.us {
    p {
        margin: 1.5rem;
    }
}