    display: block;
  }

  .text-short {
    display: none;
  }
}
.social-media h2 {
  margin-top: 0;
}

.icons {
  display: flex;
  column-gap: 25px;
  justify-content: center;
  margin-bottom: 3rem;
}

.icon {
  width: 60px;
  height: 60px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 26px;
  border-radius: 50%;
  transition-property: outline-offset, outline-color, background-color;
  transition-duration: 0.25s;
}
.icon:hover {
  outline-offset: 4px;
}
.icon:hover i {
  animation: shake 0.25s;
}

.icon--instagram {
  color: #A02D76;
  outline: 2px solid #A02D76;
}

.icon--facebook {
  color: #3a589e;
  outline: 2px solid #3a589e;
}

.icon--instagram:hover {
  background-image: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  outline-color: #A02D76;
  color: #ffffff;
}

.icon--facebook:hover {
  color: #ffffff;
  background-color: #3a589e;
  outline-color: #3a589e;
}

@keyframes shake {
  10% {
    transform: rotate(15deg);
  }
  20% {
    transform: rotate(-15deg);
  }
  30% {
    transform: rotate(15deg);
  }
  40% {
    transform: rotate(-15deg);
  }
}