@use "base/mixins" as m;
@use 'base/variables' as v;

nav {
  button {
    border: 0 solid white;
    margin: 0;
    padding: 0;
    background-color: transparent;
    img {
      padding-right: 1rem;
      padding-left: 1rem;
      height: 2rem;
      width: auto;
      opacity: 0.5;
      transition: all 0.5s;
      &:hover {
        opacity: 1;
        transition: all 0.5s;
      }
    }
  }
  p {
    text-align: center;
    padding-bottom: 1rem;
  }
}

.navbar-brand {
  width: 100%;
  @include m.telefono {
    width: auto;
  }
  img {
    max-height: 18rem;
    max-width: 20rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    @include m.telefono {
      height: 9rem;
      width: 10rem;
      margin: 0;
    }
  }
}

.active {
  opacity: 1 !important;
}

.btn-language {
  display: flex;
  justify-content: center;
  margin-bottom: 3.1rem;
  @include m.desktop {
    margin-bottom: 0;
  }
}

.navbar {
  display: block;
  a {
    font-size: 1.8rem;
    text-align: center;
  }
  @include m.tablet {
    a {
      width: 11rem;
    }
  }
  @include m.desktop {
    li {
      padding-left: 3rem;
    }
    a {
      font-size: 2.15rem;
    }
  }
  .container-fluid {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.navbar-nav {
  margin-bottom: 2rem;
}

.navbar-light .navbar-toggler {
  color: transparent;
}

.navbar-light .navbar-nav .nav-link {
  transition: color 0.5s;
  transition: border-bottom 0.5s;
  border: 1px solid transparent;
  &:hover {
    color: v.$secundario;
    border-bottom: 1px solid v.$secundario;
  }
}

.navbar-toggler {
  padding: 1rem 0.75rem;
  margin: 1rem auto 2rem auto;
  border: 2px solid transparent;
  @include m.telefono {
    margin: 0 1rem 0 0;
  }
}

.nav-item {
  display: flex;
  justify-content: center;
}

#navMenu > span {
  display: block;
  width: 28px;
  height: 2px;
  border-radius: 9999px;
  background-color: v.$grisOscuro;
}

#navMenu > span:not(:last-child) {
  margin-bottom: 7px;
}

#navMenu, #navMenu > span {
  transition: all .4s ease-in-out;
}

#navMenu.active {
  transition-delay: 0.4s;
  transform: rotate(45deg);
}

#navMenu.active > span:nth-child(2) {
  width: 0;
}

#navMenu.active > span:nth-child(1),
#navMenu.active > span:nth-child(3) {
  transition-delay: .2s;
}

#navMenu.active > span:nth-child(1) {
  transform: translateY(9px);
}

#navMenu.active > span:nth-child(3) {
  transform: translateY(-9px) rotate(90deg);
}

@include m.telefono {
  .fixed-top-nav {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
  }
}

@include m.telefono {
  .spacing {
    margin-top: 106.25px;
  }
}