@use 'base/variables' as v;
@use 'base/mixins' as m;

footer {
    background-color: v.$primario;
    color: v.$blanco;

    p {
        margin: 0.5rem;
    }
    .display-large {
        display: none;
        @include m.telefonoSM {
            display: inline;   
        }
    }
    .name-extra {
        display: block;
        @include m.telefonoSM {
            display: inline;
        }
    }
}